.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 3px solid #d34667;
  border-radius: 10px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  color: #fff;
  background-color: #ee99ad;
  border: 3px solid #d34667;
  padding: 8px 12px;
}

.feedback-form button:hover {
  box-shadow: 3px 3px 4px 4px #ccc;
}

input, textarea {
  font: inherit;
  border: 3px solid #d34667;
  border-radius: 10px;
  padding: 8px;
}

input:hover, textarea:hover {
  border-color: #704f57;
}

textarea {
  resize: none;
}

